.container {
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  position: relative;
}
.about {
  position: relative;
  padding-bottom: 60px;
}
.about .heading {
  margin-top: 40px;
  margin-bottom: 40px;
  padding: 20px 0;
  text-align: center;
}
.about .para {
  max-width: 800px;
  margin: 0 auto;
}
.about .content {
  width: 80%;
}
.rect {
  width: 800px;
  min-height: 200px;
  margin: 0 auto;
  text-align: left;
  padding: 12px 40px 12px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  background-color: #EEF1F5;
  box-shadow: 0px 5px 10px rgba(120, 130, 147, 0.4);
}
.rect .content {
  margin: 0 auto;
  vertical-align: middle;
}
.rect .heading {
  color: #1652f0;
  font-size: 22px;
  max-width: 530px;
  align-self: flex-start;
  line-height: 2;
}
.rect .para {
  text-align: left;
  max-width: 530px;
}
.rect {
  width: 624px;
  min-height: 230px;
  padding: 12px 30px 22px 30px;
}
.rect .heading {
  max-width: 600px;
}
.rect .para {
  max-width: 600px;
}
.find {
  height: 24rem;
  background: url(../../images/left_cloud_solution.png) no-repeat left center;
  background-size: contain;
}
.hero {
  overflow-y: hidden;
  display: relative;
}
.top-nav {
  position: fixed;
  top: 0;
  background-color: #FFFFFF;
  z-index: 100;
}
.hero-content {
  height: 100%;
}
.hero-content .left {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin-left: 3.75rem;
  height: 80%;
  justify-content: center;
}
.hero-content .right {
  width: 60%;
  height: 85%;
  background: url(../../images/about/hero.png) no-repeat right center;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  top: 100px;
  right: 0;
  z-index: -2;
}
.title {
  font-family: "Noto Sans SC";
  font-weight: 400;
  color: #20242B;
  font-size: 3.125rem;
  line-height: 67px;
}
.title span {
  font-weight: 300;
  color: #1652F0;
  font-size: 1.25rem;
  line-height: 1rem;
}
.download {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 3.5rem;
  width: 100%;
}
.download-btn:first-child {
  margin-right: 1rem;
}
.info {
  margin: 100px auto;
  display: flex;
  justify-content: flex-start;
  position: relative;
  width: 1200px;
}
.info .txt-box {
  margin-bottom: 40px;
}
.info h3,
.info h4 {
  margin-bottom: 16px;
  font-size: 22px;
}
.info .txt {
  margin-bottom: 16px;
}
.menu-opt {
  font-family: "Noto Sans SC";
  font-size: 1.25rem;
  font-weight: 400;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1.25rem;
  color: #677382;
  margin-left: 3.75rem;
  margin-right: 3.75rem;
}
.menu-opt p {
  min-width: 230px;
}
.menu-opt img {
  width: 32px;
  height: 32px;
}
.menu-opt span {
  margin-right: 1rem;
}
.menu-opt:hover {
  background-color: #EEF1F5;
  border-radius: 3px;
  color: #1652F0;
  cursor: pointer;
}
.selected {
  background-color: #EEF1F5;
  border-radius: 3px;
  color: #1652F0;
}
.text {
  color: #677382;
  font-size: 1rem;
  font-weight: 400;
  font-family: "Open Sans";
  line-height: 1.56rem;
}
.text span {
  font-size: 1.25rem;
  color: #000000;
  font-weight: 400;
}
.illustration-cloud {
  width: 45rem;
  height: 100%;
  background: url(../../images/about/cloud_right.jpg) no-repeat right center;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  z-index: -3;
  transform: translate(125%, 0);
}
.illustration-rocket {
  width: 25.5rem;
  height: 100%;
  background: url(../../images/about/rocket.png) no-repeat right top;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  top: 400px;
  left: 0;
  z-index: -1;
  transform: translate(-40%, 0);
}
.text.understand {
  max-width: 700px;
}
.text.ceo {
  max-width: 700px;
}
.text.history {
  max-width: 700px;
}
.profile {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
}
.profile img {
  width: 10.625rem;
  border-radius: 50%;
}
.profile-pic {
  width: 10.625rem;
  height: 10.625rem;
  border-radius: 50%;
  margin-right: 1rem;
}
.profile-info {
  width: 70%;
  margin: 0 auto;
}
.profile-info .name {
  color: #1652F0;
  font-size: 1.5rem;
  font-family: "Open Sans";
  font-weight: 400;
}
.profile-info .name span {
  font-weight: 300;
  font-size: 1rem;
  color: #677382;
}
.profile-info .quote {
  color: #000000;
  font-weight: 600;
  line-height: 1.56rem;
  margin-top: 1rem;
}
.timeline-heading {
  font-family: "Noto Sans SC";
  font-size: 1.125rem;
  color: #1652F0;
  border-bottom: 3px solid #EEF1F5;
  padding-bottom: 0.75rem;
  width: 160px;
  text-align: center;
}
.time {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2rem;
  flex-wrap: wrap;
}
.time span {
  font-family: "Noto Sans SC";
  font-size: 1.125rem;
  color: #1652F0;
}
.time .subtitle {
  font-family: "Noto Sans SC";
  font-size: 1.125rem;
  color: #1652F0;
  margin-bottom: 0.5em;
  display: inline-block;
  width: 80%;
  margin-top: 0.5rem;
}
.time .row {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.time .row:nth-child(2) {
  margin-bottom: 1rem;
}
.time .circle {
  border: 2px solid #1652F0;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  display: inline-block;
  margin-right: 0.75rem;
}
.time .year {
  display: inline-block;
  margin: 0 1em 1em 0;
}
.time .year span {
  font-weight: 900;
}
.time .year.hide {
  opacity: 0;
}
.time .event {
  margin-left: 1.25rem;
  line-height: 1.625rem;
  flex-basis: 40%;
  margin-bottom: 1.25em;
}
.time .subtitle.included {
  margin-top: 1rem;
}
.time .month {
  margin-top: 0.5rem;
}
.month {
  color: #677382;
  opacity: 0.5;
  font-size: 2rem;
  font-weight: 300;
  vertical-align: top;
  display: inline-block;
  font-family: "Noto Sans SC";
}
@media only screen and (max-width: 414px) {
  .container {
    min-height: initial;
    min-width: initial;
    width: 100%;
    height: auto;
    padding-bottom: 160px;
  }
  .illustration-cloud {
    display: none;
  }
  .illustration-rocket {
    display: none;
  }
  .hero-content {
    padding: 68px 1rem 0;
  }
  .top-nav {
    position: relative;
  }
  .hero .hero-content .left {
    width: auto;
    height: auto;
    margin: 0 auto;
  }
  .hero .hero-content .left .title {
    padding: 0 0.125rem 0 0.125rem;
    line-height: 60px;
    text-align: center;
  }
  .hero .hero-content .left .strong {
    font-size: 24px;
  }
  .hero .hero-content .right {
    transform: translate(0, 30%);
    background-size: contain;
    width: 100%;
    height: 400px;
    background-position: top left;
  }
  .hero .download {
    margin-top: 2rem;
    align-items: center;
    justify-content: space-around;
  }
  .hero .download .download-btn img {
    height: 48px;
  }
  .info {
    width: auto;
    display: block;
  }
  .info .menu-opt {
    flex: 1;
    margin: 0;
    font-size: 14px;
    padding: 1rem 0.4rem;
    justify-content: center;
  }
  .info .menu-opt p {
    min-width: initial;
    text-align: center;
  }
  .info .ceo .profile {
    display: block;
  }
  .info .ceo .profile .profile-pic {
    width: 100%;
    text-align: center;
  }
  .info .ceo .profile .profile-info {
    width: 100%;
    margin-top: 12px;
  }
  .info .ceo .profile .profile-info .name {
    text-align: center;
  }
  .info .history .time {
    padding: 1rem 0;
  }
  .menu {
    display: flex;
    flex-basis: 30%;
    width: 100%;
    justify-content: space-around;
  }
  .menu .menu-opt span {
    display: none;
  }
  .text {
    margin-top: 2rem;
    padding: 0 1rem 0 1rem;
  }
  .time .event {
    flex-basis: 100%;
  }
}
