.container {
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
    position: relative;
}

.about {
    position: relative;
    padding-bottom: 60px;
    .heading {
        margin-top: 40px;
        margin-bottom: 40px;
        padding: 20px 0;
        text-align: center;
    }
    .para {
        max-width: 800px;
        margin: 0 auto;
    }
    .content {
        width: 80%;
    }
}

.rect {
    width: 800px;
    min-height: 200px;
    margin: 0 auto;
    text-align: left;
    padding: 12px 40px 12px 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    background-color: @background_color_2;
    box-shadow: 0px 5px 10px rgba(120, 130, 147, 0.4);
    .content {
        margin: 0 auto;
        vertical-align: middle;
    }
    .heading {
        color: #1652f0;
        font-size: 22px;
        max-width: 530px;
        align-self: flex-start;
        line-height: 2;
    }
    .para {
        text-align: left;
        max-width: 530px;
    }
}
.rect {
    width: 624px;
    min-height: 230px;
    padding: 12px 30px 22px 30px;
    .heading {
        max-width: 600px;
    }
    .para {
        max-width: 600px;
    }
}

.find {
    height: 24rem;
    background: url(../../images/left_cloud_solution.png) no-repeat left center;
    background-size: contain;
}


.hero {
    overflow-y: hidden;
    display: relative;
}
.top-nav {
    position: fixed;
    top: 0;
    background-color: @background_color_1;
    z-index: 100;
}
.hero-content {
    height: 100%;
    .left {
        display: flex;
        flex-direction: column;
        width: 40%;
        margin-left: 3.75rem;
        height: 80%;
        justify-content: center;
    }
    .right {
        width: 60%;
        height: 85%;
        background: url(../../images/about/hero.png) no-repeat right center;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        position: absolute;
        top: 100px;
        right: 0;
        z-index: -2;
    }
}
.title {
    font-family: @font_family_1;
    font-weight: 400;
    color: @color_1;
    font-size: 3.125rem;
    line-height: 67px;
    span {
        font-weight: 300;
        color: @color_2;
        font-size: 1.25rem;
        line-height: 1rem;
    }
}
.download {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 3.5rem;
    width: 100%;
}
.download-btn {
    &:first-child {
        margin-right: 1rem;
    }
}
.info {
    margin: 100px auto;
    display: flex;
    justify-content: flex-start;
    position: relative;
    width: 1200px;
    .txt-box {
        margin-bottom: 40px;
    }
    h3, h4 {
        margin-bottom: 16px;
        font-size: 22px;
    }

    h3 {

    }

    h4 {

    }

    .txt {
        margin-bottom: 16px;
    }

}
.menu-opt {
    font-family: @font_family_1;
    font-size: 1.25rem;
    font-weight: 400;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 1.25rem;
    color: @color_3;
    margin-left: 3.75rem;
    margin-right: 3.75rem;
    p {
        min-width: 230px;
    }
    img {
        width: 32px;
        height: 32px;
    }
    span {
        margin-right: 1rem;
    }
    &:hover {
        background-color: @background_color_2;
        border-radius: 3px;
        color: @color_2;
        cursor: pointer;
    }
}
.selected {
    background-color: @background_color_2;
    border-radius: 3px;
    color: @color_2;
}
.text {
    color: @color_3;
    font-size: 1rem;
    font-weight: 400;
    font-family: @font_family_2;
    line-height: 1.56rem;
    span {
        font-size: 1.25rem;
        color: @color_4;
        font-weight: 400;
    }
}
.illustration-cloud {
    width: 45rem;
    height: 100%;
    background: url(../../images/about/cloud_right.jpg) no-repeat right center;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    z-index: -3;
    transform: translate(125%, 0);
}
.illustration-rocket {
    width: 25.5rem;
    height: 100%;
    background: url(../../images/about/rocket.png) no-repeat right top;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    -o-background-size: contain;
    background-size: contain;
    position: absolute;
    top: 400px;
    left: 0;
    z-index: -1;
    transform: translate(-40%, 0);
}
.text.understand {
    max-width: 700px;
}
.text.ceo {
    max-width: 700px;
}
.text.history {
    max-width: 700px;
}
.profile {

    display: flex;
    flex-flow: row;
    justify-content: space-between;
    img {
        width: 10.625rem;
        border-radius: 50%;
    }
}
.profile-pic {
    width: 10.625rem;
    height: 10.625rem;
    border-radius: 50%;
    margin-right: 1rem;
}
.profile-info {
    width: 70%;
    margin: 0 auto;
    .name {
        color: @color_2;
        font-size: 1.5rem;
        font-family: @font_family_2;
        font-weight: 400;
        span {
            font-weight: 300;
            font-size: 1rem;
            color: @color_3;
        }
    }
    .quote {
        color: @color_4;
        font-weight: 600;
        line-height: 1.56rem;
        margin-top: 1rem;
    }
}
.timeline-heading {
    font-family: @font_family_1;
    font-size: 1.125rem;
    color: @color_2;
    border-bottom: 3px solid #EEF1F5;
    padding-bottom: 0.75rem;
    width: 160px;
    text-align: center;
}
.time {
    span {
        font-family: @font_family_1;
        font-size: 1.125rem;
        color: @color_2;
    }
    .subtitle {
        font-family: @font_family_1;
        font-size: 1.125rem;
        color: @color_2;
        margin-bottom: 0.5em;
        display: inline-block;
        width: 80%;
        margin-top: 0.5rem;
    }
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 2rem;
    flex-wrap: wrap;
    .row {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        &:nth-child(2) {
            margin-bottom: 1rem;
        }
    }
    .circle {
        border: 2px solid #1652F0;
        border-radius: 50%;
        width: 0.5rem;
        height: 0.5rem;
        display: inline-block;
        margin-right: 0.75rem;
    }
    .year {
        display: inline-block;
        margin: 0 1em 1em 0;
        span {
            font-weight: 900;
        }
    }
    .year.hide {
        opacity: 0;
    }
    .event {
        margin-left: 1.25rem;
        line-height: 1.625rem;
        flex-basis: 40%;
        margin-bottom: 1.25em;
    }
    .subtitle.included {
        margin-top: 1rem;
    }
    .month {
        margin-top: 0.5rem;
    }
}
.month {
    color: @color_3;
    opacity: 0.5;
    font-size: 2rem;
    font-weight: 300;
    vertical-align: top;
    display: inline-block;
    font-family: @font_family_1;
}
.footer {
    //background-color: #fafafa;
}
@media only screen and (max-width: 414px) {
    .container {
        min-height: initial;
        min-width: initial;
        width: 100%;
        height: auto;
        padding-bottom: 160px;
    }
    .illustration-cloud {
        display: none;
    }
    .illustration-rocket {
        display: none;
    }
    .hero-content {
        padding: 68px 1rem 0;
    }
    .top-nav {
        position: relative;
    }
    .hero {
        .hero-content {
            .left {
                width: auto;
                height: auto;
                margin: 0 auto;
                .title {
                    padding: 0 0.125rem 0 0.125rem;
                    line-height: 60px;
                    text-align: center;
                }
                .strong {
                    font-size: 24px;
                }
            }
            .right {
                transform: translate(0, 30%);
                background-size: contain;
                width: 100%;
                height: 400px;
                background-position: top left;
            }
        }
        .download {
            margin-top: 2rem;
            align-items: center;
            justify-content: space-around;
            .download-btn {
                img {
                    height: 48px;
                }
            }
        }
    }
    .info {
        width: auto;
        display: block;
        .menu-opt {
            flex: 1;
            margin: 0;
            font-size: 14px;
            padding: 1rem .4rem;
            justify-content: center;
            p {
                min-width: initial;
                text-align: center;
            }
        }
        .ceo {
            .profile {
                display: block;
                .profile-pic {
                    width: 100%;
                    text-align: center;
                }
                .profile-info {
                    width: 100%;
                    margin-top: 12px;
                    .name {
                        text-align: center;
                    }
                }
            }
        }
        .history {
            .time {
                padding: 1rem 0;
            }
        }
    }
    .menu {
        display: flex;
        flex-basis: 30%;
        width: 100%;
        justify-content: space-around;
        .menu-opt {
            span {
                display: none;
            }
        }
    }
    .text {
        margin-top: 2rem;
        padding: 0 1rem 0 1rem;
    }
    .time {
        .event {
            flex-basis: 100%;
        }
    }
}
